import React from "react";
import { getDocument } from "api/collections";
import { Document as DocumentType } from "utils/interfaces";
import { useSelector } from "react-redux";
import { RootState } from "store";
import { useHistory } from "react-router-dom";
import DocumentForm from "components/DocumentForm";
import CollectionsManager from "facades/CollectionsManager";

export interface DocumentProps {
  collectionId: string;
  docId: string;
}

const Document: React.FC<DocumentProps> = ({ collectionId, docId }) => {
  const collectionType = useSelector((state: RootState) =>
    state.collectionTypes.find((x) => x.id === collectionId)
  );

  const [doc, setDoc] = React.useState<DocumentType | null>(null);

  React.useEffect(() => {
    (async () => {
      try {
        if (!collectionType) return;
        if (!docId && !collectionType.single) return;
        const res = await getDocument(collectionId, docId);

        if (!res.error) {
          setDoc(res);
        }
      } catch (error) {
        console.log(error);
      }
    })();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [docId, collectionId, collectionType]);

  const history = useHistory();

  return !collectionType ? null : (
    <DocumentForm
      level={0}
      groundColor="black"
      docId={docId}
      editableDocument={doc ? doc : undefined}
      fields={collectionType.fields}
      onSubmit={async (vals) => {
        if (vals) {
          if (doc != null) {
            CollectionsManager.updateDocument(collectionType.id, doc, vals);
          } else {
            CollectionsManager.addDocument(collectionType.id, vals);
          }
        }

        if (!collectionType.single) {
          history.goBack();
        }
      }}
    ></DocumentForm>
  );
};

export default Document;
